<template>
  <db-bill-entry
    title="Bills during 2nd pay period"
    pay-period="2"
    :sub-title="subTitle"
  />
</template>

<script>
  import welcome from './welcome';
  import { Settings } from './main';
  import dbBillEntry from './BillEntry';

  export default {
    components: {
      dbBillEntry,
    },

    computed: {
      subTitle () {
        var start = Settings.payDay2.format("dddd, MMM Do"),
            ending = Settings.payDay2.endOf("month")
                                     .subtract(1, "days")
                                     .format("dddd, MMM Do");

        return start + " – " + ending;
      },
    },

    mounted () {
      welcome.activate({
        next: {
          path: "/dashboard",
          name: "Dashboard",
        },

        prev: {
          path: "/pay_period/1",
          name: "1st pay period",
        },
      });
    },

    destroyed () {
      welcome.reset();
    },
  };
</script>

<style lang="scss" scoped>
</style>



