import Vue from 'vue';

import Storage from './utils/Storage';

export default new Vue({
  data() {
    return {
      bills: Storage.get(
        "payDayBills",
        {
          parser: "JSON",
          default: [],
        }
      ),
    };
  },

  computed: {
    nextKey () {
      var ids = this.bills.map(b => { return b.id });
      return (Math.max(...ids) || 0) + 1;
    },
  },

  watch: {
    bills: {
      deep: true,
      handler (current) {
        Storage.set("payDayBills", current, { formatter: "JSON" });
        this.$emit("updatedBills", current);
      },
    },
  },

  methods: {
    add (bill) {
      bill.isEditing = false;
      bill.id = this.nextKey;
      this.bills.unshift(bill);
    },

    remove (id) {
      var idx = this.bills.findIndex((bill) => {
        return bill.id == id;
      });
      this.bills.splice(idx, 1);
    },
  },
});



