<template>
  <div>
    <ul>
      <li v-for="item in ledgerItems">
        {{ item.category }} - {{ item.amount | to-currency }}
      </li>
    </ul>

    <p>Remaining: {{ amountRemaning | to-currency }}</p>
  </div>
</template>

<script>
  import moment from 'moment';
  import { Settings } from './main';
  import payDayBills from './PayDayBills';

  export default {
    data () {
      return {
        ledgerItems: payDayBills.bills.filter(b => {
          let today = moment().startOf("day"),
              currentPayPeriod;

          if (today >= Settings.payDay2.startOf("day")) {
            currentPayPeriod = 2;
          } else {
            currentPayPeriod = 1;
          }

          return parseInt(b.payPeriod) == currentPayPeriod;
        }),
      };
    },

    computed: {
      amountRemaning () {
        return Settings.income - this.ledgerItems.reduce((acc, item) => {
          return acc += item.amount;
        }, 0);
      },
    },
  };
</script>

<style lang="scss" scoped>
</style>



